body {
  margin: 0;
  font-family: "Noto Sans KR", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
* {
  box-sizing: border-box;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.black-nav {
  display: flex;
  width: 100%;
  background-color: black;
  justify-content: space-between;
  flex-flow: row nowrap;
  align-items: center;
  padding: 14px 20px;
}
.title {
  font-size: 1.4rem;
  font-weight: 500;
  color: white;
}
#dev-list {
  background-color: #eee;
  height: 100vh;
}
#dev-list .list {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  gap: 30px;
  padding: 0px 20px;
  border-bottom: 1px solid #eee;
  font-size: 0.9rem;
  color: #666;
  height: 50px;
  background-color: white;
}
#dev-list .sub-ject-title {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  font-size: 1rem;
}
#dev-list .like {
  display: inline-flex;
  font-size: 0.8rem;
  font-weight: 400;
  color: blue;
  margin-left: 20px;
  width: 60px;
  justify-content: flex-start;
  align-items: center;
}
#dev-list .like > button {
  display: inline-flex;
  gap: 6px;
}
#dev-list .list .reg-date {
  width: 160px;
  text-align: right;
}
.btn {
  margin-left: 20px;
  margin-right: 20px;
}
.modal {
  padding: 20px;
  background: #eee;
  text-align: left;
  display: block;
  position: absolute;
  width: 90vw;
  height: 90vh;
  z-index: 99;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}
.btn-area {
  flex: 1;
}
#dev-list .input-box {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
}
#dev-list .input-box form {
  display: flex;
  align-items: center;
  gap: 10px;
}
#dev-list .input-box button {
  width: 120px;
}
